import React, {useContext} from 'react';
import Alert from '../../../components/dialog/alert';
import { GlobalContext } from '../../../store/context';
import {FcAlarmClock} from 'react-icons/fc' 
export default function SectionAlerts() {
    const {ALERT = [], onToggleAlert} = useContext(GlobalContext);
	return <>
        {
            ALERT.map(({type,open = false,typeModal, title, text},index)=>
                open == true &&
                <Alert open={open} callback={onToggleAlert(type)} typeModal={typeModal}>
                   <FcAlarmClock size="64"/>
                    <div className="modal_title">{title}</div>
                    <h5 className="modal_text">{text}</h5>
                </Alert>
            )
        }  
    </>
}
